import React from "react";

const Environmentalconsultancy = () => {
  return (
    <div id="enviromental-consultancy" className="mt40">
      <div className="row">
        <div className="col-md-10 col-md-offset-1">
          <h2>Physical Planning Solutions</h2>
          <img src="images/environmental-consultancy/bg1.jpg" width="100%" />
          <p>
            In pursuit to contributing in various development agendas and
            initiatives, Geoenvis Africa remains very conscious to
            environmentally friendly sustainable growth. We have a vibrant
            environmental team with expertise in various sub-disciplines of
            environmental studies namely: Soil, Water and Environment Engineers,
            ASALs Environmentalists, Natural Resources Management Experts,
            Climatologists, Oceanographers and Bio-Geographers
          </p>
          <p>
            We apply GIS and Remote Sensing in offering the following
            consultancy services:
          </p>
          <h3>1. Environmental Impact Assessment (EIA)</h3>
          <h3>2. Environmental Auditing (EA)</h3>
          <h3>3. Climate Change</h3>
          <h3>4. Natural Resources Management</h3>
          <h3>5. ASALs Resilience</h3>
          <h3>6. Water, Sanitation and Hygiene (WaSH) </h3>
          <h3>7. Wetlands Conservancy</h3>
        </div>
      </div>
    </div>
  );
};

export default Environmentalconsultancy;



